body {
  height: 1300px;
  margin: auto;
}

.search-container {
  height: 60px;
  width: 100%;
  background-color: #6495ed;
  justify-content: center;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.search-form {
  display: flex;
}

.search-input {
  height: 20px;
  width: 220px;
  cursor: text;
  color: #01405f;
  border: 1px solid #01405f;
  border-radius: 5px;
  outline: none;
  padding: 5px;
}

.search-input:focus {
  border: 2px solid #01405f;
}

.search-btn {
  color: #959697;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
  transform: translate(-105%);
}

